// scss 全局变量
$fontColor: #808083;
$smallestFontSize: 10px;
$smallFontSize: 12px;
$middleFontSize: 15px;
$bigFontSize: 16px;
$inputMarginBottom: 10px;
$yxLightGreen: #2dbe61;
$errorTipColor: #cd5450;

@mixin basicButton {
  margin-top: 20px;
  border:1px solid rgba(151,151,151,1);
  color:rgba(77,77,77,1);
  background-color: #fff;
}

@mixin linkButton {
  background: none;
  border: none;
  font-size: 13px;
  height:18px;
  min-height: 18px;
  margin-top: 20px;
  color:rgba(77,77,77,1);
  line-height:18px;
  -webkit-tap-highlight-color: transparent;
}

@mixin hide {
  &.hide {
    display: none;
  }
}

@mixin vercodeHelpLinkWrapper {
  .vercodeHelpLinkWrapper {
    margin: 20px 0 0;
    text-align: center;
    @include hide;
  }
}

@mixin wechatButtonWrapper {
  .wechatButtonWrapper {
    margin-top: 16px;
    border: 1px solid #cacaca;
    font-size: $smallFontSize;
    color: #848a8b;
    background: #fff;

    &:hover {
      background: #f8f8f8;
    }
  }
}

@mixin centerLayout {
  width: 380px;
  margin: 0 auto;
}

@mixin globalBorderTop {
  border-top: 6px solid $yxLightGreen;
}

@mixin linkStyleButton {
  cursor: pointer;
  border: none;
  background: transparent;
  outline: none;
}

@mixin clearfix {
  &:after {
    display: block;
    height: 0;
    content: '.';
    visibility: hidden;
    clear: both;
  }
}

@mixin containedBackground($url) {
  background: url($url);
  background-size: contain;
}

@mixin retinaBackground($url, $url2x, $url3x) {
  @include containedBackground($url);
  @if $url2x {
    @media screen and (-webkit-min-device-pixel-ratio: 2) {
      @include containedBackground($url2x);
    }
  }
  @if $url3x {
    @media screen and (-webkit-min-device-pixel-ratio: 3) {
      @include containedBackground($url3x);
    }
  }
}
